
#header {
  height: 53px;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  box-sizing: border-box;
  padding: 0 2em;
  width: 100%;
  height: 3.3em;
  background-color: #fff;
  box-shadow: 0 1px 10px 0 rgba(0, 34, 77, 0.05);
  z-index: 998;
}
.header-menu__search {
    padding-bottom: 1px;
    border-bottom: 2px solid transparent;
    transition: .3s ease;
    letter-spacing: -3px;
}
.header-menu__search.active {
      border-bottom: 2px solid #6181E7;
}
.header-menu__search.active .header-menu__input {
        width: 10em;
}
.header-menu__magnifying {
    font-size: 1em;
    color: #6181E7;
    cursor: pointer;
}
.header-menu__input {
    width: 0;
    border: 0;
    font-size: 0.9em;
    transition: .2s ease;
}
.menu-btn {
  position: fixed;
  left: 2em;
  top: 0.9em;
  width: 25px;
  height: 25px;
  z-index: 9999;
  cursor: pointer;
}
.menu-line {
    position: relative;
    top: 11px;
    width: 25px;
    height: 3px;
    background-color: #6181E7;
    transition: .3s ease;
}
.menu-line:before {
      position: absolute;
      top: -8px;
      content: ' ';
      width: 25px;
      height: 3px;
      background-color: #6181E7;
      transition: .3s ease;
}
.menu-line:after {
      position: absolute;
      top: 8px;
      content: ' ';
      width: 25px;
      height: 3px;
      background-color: #6181E7;
      transition: .3s ease;
}
.menu-line.active {
      width: 0;
}
.menu-line.active:before {
        -webkit-transform: translateY(8px) rotate(45deg);
                transform: translateY(8px) rotate(45deg);
}
.menu-line.active:after {
        -webkit-transform: translateY(-8px) rotate(-45deg);
                transform: translateY(-8px) rotate(-45deg);
}



